@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@400;500&family=Mulish:wght@700;800&family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body {
  background-color: #F9FAFD;
}

.App {
  text-align: center;
  background-color: #F9FAFD;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
}

h1 {
  font-family: 'Mulish', sans-serif;
  font-weight: 800;
  font-size: 28pt;
  letter-spacing: 0.5pt;
  color: #092E50;
}

.About {
  position: relative;
  display: block;
  margin: 5px;
  max-width: 1080px;
  top: 50px;
  align-self: center;
  margin-bottom: 100px;
  font-size: calc(10px + 2vmin);
}

.shape {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 1000px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 50;
  top: -10% !important;
}



.row {
  position: relative;
  margin: 5px;
  max-width: 1080px;
  display: block;
  align-self: center;
  display: flex;
}

.Experiences {
  float: left;
  width: 800px;
  margin: 15px;
  padding: 5;
  text-align: left;
}

.Projects {
  text-align: left;
  margin-right: 0px;
  margin-top: 15px;
  float: right;
  width: 300;
  padding: 5;
}

@media screen and (max-width: 600px) {
  .Projects
  .Experiences {
    width: 100%;
  }
}

.shapes {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.bg-shape {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -90% !important;
  width: 95%;
  right: 0;
  z-index: -1;
}
.bg-shapes {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -90% !important;
  width: 95%;
  right: 0;
  z-index: -1;
}

