.Exp {
    height: 100px;
    border-bottom: solid;
    border-width: 1px;
    border-color:#3C64B1;
    margin-bottom: 25px;
    padding: 15px;
    margin-right:25px;
}

.header {
    float: left;
    width: 80%;
}

.descrip {
    float: left;
    width: 80%;
}

.date {
    width: 20%;
    float: right;
    text-align: right;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 11pt;
}
.Proj {
    align-content: right;
    width: 300px;
    height: 150px;
    overflow: hidden;
    margin-bottom: 5px;
}

